@media (max-width: 1028px) {

  .stepsContainer {
    padding: 72px 0;
    margin: 0 -20px;
  }

  .steps {
    margin-top: 56px;
    font-size: 15px;
    line-height: 20px;
    padding: 0 20px;
    flex-direction: column;
  }

  .step {
    align-items: flex-start;
    text-align: left;
    margin-bottom: 38px;
    width: 85%;
  }

  .stepIcon {
    height: 44px;
    margin-right: 9%;
    margin-left: 7%;
  }

  .subTextM {
    flex-direction: column;
  }

  .subText {
    margin-top: 20px;
  }

  .stepLine {
    display: none;
  }

  .vacancies {
    margin-top: 36px;
    font-size: 15px;
    line-height: 20px;
    text-align: center;
  }

  .vacancy {
    flex-direction: column;
    width: 336px;
    margin-bottom: 36px;
  }

  .vacancyName {
    font-size: 21px;
    line-height: 32px;
    font-weight: bold;
    margin-bottom: 20px;
  }

  .vacancyDescription {
    margin-top: 15px;
    font-size: 15px;
  }

  .offerPrice {
    font-size: 15px;
    margin-top: 32px;
  }

  .featuresContainer {
    margin-bottom: 72px;
  }

  .title {
    font-size: 24px;
  }

  .features {
    margin-top: 38px;
    font-size: 15px;
    flex-direction: column;
  }

  .lie {
    align-items: center;
    text-align: left;
    margin-bottom: 38px;
    width: 85%;
    height: 40px;
  }

  .bankFeature {
    height: 30px;
    margin-right: 9%;
    margin-left: 7%;
  }

  .position {
    height: 340px;
  }
}

@media (min-width: 1028px) {

  .stepsContainer {
    padding: 72px 0;
    margin: 0 -128px;
  }

  .steps {
    margin-top: 41px;
    font-size: 17px;
    line-height: 24px;
    padding: 0 128px;
  }

  .step {
    margin-right: 48px;
    flex-direction: column;
    align-items: baseline;
    width: 240px;
  }

  .stepIcon {
    height: 64px;
    margin-bottom: 23px;
  }

  .subText {
    margin-top: 20px;
  }

  .vacancies {
    margin-top: 72px;
    font-size: 17px;
    line-height: 24px;
    text-align: center;
  }

  .vacancy {
    margin-right: 48px;
    width: 336px;
  }

  .vacancyName {
    font-size: 24px;
    font-weight: bold;
    height: 48px;
    margin-bottom: 25px;
  }

  .vacancyDescription {
    margin-top: 12px;
    font-size: 17px;
  }

  .offerPrice {
    font-size: 17px;
    margin-top: 20px;
  }

  .featuresContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 96px;
  }

  .title {
    font-size: 36px;
  }

  .features {
    width: 100%;
    max-width: 1024px;
    justify-content: space-between;
    margin-top: 100px;
    font-size: 17px;
  }

  .lie {
    width: 197px;
    margin-right: 24px;
    flex-direction: column;
  }

  .bankFeature {
    height: 70px;
    margin-bottom: 39px;
  }

  .position {
    height: 400px;
  }
}

.stepsContainer {
  background-color: #eeeefa;
}

.step {
  display: flex;
  text-align: left;
  z-index: 1;
}

.steps {
  display: flex;
  color: #333333;
  position: relative;
  z-index: 0;
}

.stepLine {
  background: url("~images/line.svg");
  width: 70%;
  max-width: 1000px;
  height: 2px;
  position: absolute;
  top: 30px;
  left: 0;
}

.title {
  font-weight: bold;
  text-align: center;
  color: #333333;
}

.stepIcon {

}


.featuresContainer {
  margin-top: 72px;
}

.features {
  display: flex;
  text-align: center;
  color: #333333;
  line-height: 24px;
}

.lie {
  display: flex;
}

.bankFeature {

}

.homeImg {
  display: flex;
  align-items: flex-end;
}

.homeImg img {
  width: 100%;
  height: 100%;
}

.important {
  font-weight: bold;
}

.openVacancies {
  margin: 72px 0;
  font-size: 17px;
  text-align: center;
}

.vacancies {
  display: flex;
  justify-content: center;
  color: #333333;
  flex-wrap: wrap;
}

.position {
  position: relative;
  padding: 24px;
  text-align: left;
}

.vacancyDescription {
  color: #333333;
}

.offerPrice {
  position: absolute;
  bottom: 104px;
  font-weight: bold;
  color: #1a9f29;
}

.goInterview {
  position: absolute;
  bottom: 25px;
  margin-top: 24px;
}

.vacancy {
  background-color: #f5f5f5;
}

.vacancyImage {
  height: 256px;
  margin-bottom: -5px;
}

