
@media (max-width: 1028px) {

  .vacancyBlock {
    display: flex;
    justify-content: center;
    margin: 0 -20px;
    padding: 0 20px;
  }

  .content {
    display: flex;
    position: relative;
    width: 100%;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  .vacancyContent {
    composes: content;
    height: 673px;
  }

  .vacancySectionImage {
    bottom: 0;
    height: 277px;
  }

  .vacancySection {
    top: 30px;
    width: 264px;
  }

  .vacancyLabel {
    font-size: 24px;
    line-height: 32px;
  }

  .vacancyPrice {
    font-size: 15px;
    line-height: 24px;
  }

  .vacancyDesc {
    font-size: 15px;
    line-height: 20px;
    margin-top: 20px;
  }

  .points {
    margin: 0 -20px;
  }

  .pointsContent {
    composes: content;
  }

  .formControl {
    /*position: absolute;*/
    margin-top: 70px;
  }

  .title {
    font-size: 24px;
    line-height: 32px;
  }

  .pointsList {
    display: flex;
    padding: 0 20px;
    flex-direction: column;
  }

  .point {
    width: 85%;
    padding-left: 28px;
    padding-top: 10px;
    margin-bottom: 28px;
    height: 160px;
  }

  .subTitle {
    font-size: 21px;
    line-height: 32px;
  }

  .listContainer {
    display: flex;
    flex-direction: column;
    height: 1888px;
  }


  .listTitle {
    font-size: 21px;
    line-height: 32px;
  }

  .listIn {
    font-size: 15px;
  }

  .listIn :before {
    margin-right: 12px;
  }

  .listHeader {
    width: 100%;
  }


  .formContent {
    background: #d6e3ec;
    composes: content;
    margin: 0 -20px;
    padding: 0 20px;
    height: 1050px;
    text-align: center;
  }


  .formBanner {
    position: absolute;
    display: flex;
    width: 100%;
    bottom: 0;
    height: 324px;
  }

  .vacancyGirl {
    object-fit: cover;
    overflow: hidden;
    margin-left: -160px;
  }

  .fromTitle {
    font-size: 32px;
    line-height: 32px;
  }

  .formDesc {
    font-size: 15px;
    line-height: 20px;
    width: 200px;
    margin-left: calc(50% - 100px);
  }

  .confirmText {
    margin-left: 9px;
    text-align: left;
  }

  .checkbox {
    align-items: flex-start;
    padding-left: 5px;
  }

  .formSubmit {
    margin-right: 130px;
  }

  .formInput {
    width: 95%;
  }

  .check {
    margin-top: 8px;
    transform:scale(1.5);
  }

}

@media (min-width: 1029px) {

  .vacancyBlock {
    display: flex;
    justify-content: center;
    height: 468px;
    margin: 0 -128px;
  }

  .vacancyPrice {
    font-size: 17px;
  }

  .vacancyDesc {
    font-size: 17px;
    margin-top: 16px;
    width: 528px;
  }

  .vacancySectionImage {
    right: 0;
    height: 468px;
  }

  .vacancySection {
    top: 56px;
    left: 0;
  }

  .vacancyLabel {
    font-size: 46px;
  }

  .points {
    display: flex;
    justify-content: center;
    margin: 0 -128px;
  }

  .title {
    font-size: 36px;
  }

  .pointsList {
    justify-content: space-between;
  }

  .point {
    width: 300px;
    padding-left: 28px;
    padding-top: 20px;
    margin-right: 52px;
  }

  .subTitle {
    font-size: 24px;
    line-height: 32px;
  }

  .listContainer {
    justify-content: center;
    padding-bottom: 72px;
    flex: 1;
  }

  .listBlock {
    width: 528px;
    margin-right: 48px;
  }

  .listTitle {
    font-size: 24px;
  }

  .listIn :before {
    margin-right: 16px;
  }

  .listHeader {
    height: 300px;
  }

  .listContent {
    height: 570px;
  }


  .form {
    margin: 0 -128px;
    min-height: 676px;
    justify-content: center;
    /*align-items: flex-end;*/
  }

  .vacancyGirl {
    height: 676px;
    margin-bottom: -5px;
  }

  .fromTitle {
    font-size: 36px;
    line-height: 48px;
  }

  .confirmText {
    margin-left: 8px;
  }

  .checkbox {
    align-items: center;
  }

  .formInput {
    width: 336px;
  }

  .formControl {
    position: absolute;
    top: 70px;
  }

  .content {
    position: relative;
    overflow: hidden;
    width: 90%;
    max-width: 1024px;
  }

  .vacancyContent {
    composes: content;
  }

  .form {
    background: #d6e3ec;
    padding: 0 20px;
    display: flex;
    height: 676px;
    /*box-sizing: border-box;*/
    z-index: 0;
  }

  .formBanner {
    position: absolute;
    bottom: 0;
    right: -150px;
  }

  .formContent {
    composes: content;
    text-align: left;
  }

}

.vacancyBlock {
  background-color: #d6e6f3;
}

.points {
  padding: 72px 0;
  background-color: #ededed;
}

.title {
  font-weight: bold;
  text-align: center;
  color: #333333;
  margin-bottom: 40px;
}

.subTitle {
  font-weight: bold;
  color: #333333;
}

.pointsList {
  display: flex;
}

.sellPoint {
  composes: point;
  border-left: 4px #2ca53a solid;
}

.helpPoint {
  composes: point;
  border-left: 4px #9b3ad8 solid;
}

.suggestPoint {
  composes: point;
  border-left: 4px #4497e3 solid;
}

.pointDescription {
  color: #333333;
  font-size: 17px;
  line-height: 24px;
  margin-top: 16px;
}

.vacancySection {
  position: absolute;
}

.vacancyLabel {
  font-weight: bold;
  color: #333;
}

.vacancyDesc {
  color: #333333;
}

.vacancyDesc span {
  font-weight: bold;
}

.vacancyPrice {
  margin-top: 20px;
  font-weight: bold;
  color: #1a9f29;
}

.vacancyButton {
  margin-top: 28px;
}

.listContainer {
  display: flex;

}

.listBlock {
  margin-top: 40px;
}


.listContent {
  display: flex;
  flex-direction: column;
  flex: 1;
  background: #f5f5f5;
  padding: 25px;
  margin-top: -5px;
}

.listTitle {
  font-weight: bold;
}

.listIn {
  margin-top: 24px;
  padding-left: 0;
}

.listIn li {
  margin-top: 20px;
  list-style: none;
}

.listIn li::before{
  content: '';
  display: inline-block;
  width: 12px;
  height: 12px;
  background-image: linear-gradient(to bottom, #02d08f, #03b09a);
  border-radius: 100%;
}

.listOut {
  margin-top: 24px;
  padding-left: 0;
}

.listOut li {
  margin-top: 20px;
  list-style: none;
}

.listOut li::before{
  content: '';
  display: inline-block;
  margin-right: 16px;
  width: 12px;
  height: 12px;
  background-image: linear-gradient(to right, #25aff6, #2ecefa);
  border-radius: 100%;
}


.formField {
  margin-bottom: 16px;
}

.formInput {
  display: block;
  outline: none;
  height: 48px;
  background: #f2f2f2;
  border-radius: 4px;
  border: solid 1px #d7d7d7;
  padding-left: 12px;
  font-size: 17px;
  color: #333333;
}

.formSubmit {
  margin-top: 32px;
  margin-bottom: 20px;
  /*background-image: linear-gradient(to bottom, #02d08f, #03b09a);*/
}

.formTitle {
  font-size: 24px;
  font-weight: bold;
}

.formDesc {
  margin-top: 10px;
}

.formControl {
  z-index: 1;
}



.vacancySectionImage {
  position: absolute;
}

.fromTitle {
  font-weight: bold;
  color: #333333;
}

.formBlock {
  margin-top: 26px;
}

.error {
  text-align: left;
  padding-left: 7px;
  margin-top: 7px;
  font-size: 13px;
  color: red;
}

.checkbox {
  display: flex;
  margin-top: 20px;
}

.confirmText {
  color: #333333;
}
