
@media (max-width: 1028px) {

  .mainBanner {
    bottom: 0;
    height: 260px;
  }

  .content {
    display: flex;
    position: relative;
    overflow: hidden;
    width: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0 -20px;
    padding: 0 20px;
  }

  .bannerContent {
    composes: content;
    background-color: #d6e6f3;
    height: 570px;
  }

  .homeSection {
    text-align: center;
    width: 254px;
    top: 36px;
  }

  .bannerTitle {
    font-size: 24px;
    line-height: 32px;
  }

  .bannerSubTitle {
    font-size: 15px;
    line-height: 20px;
  }

  .interviewContent {
    composes: content;
    text-align: center;
    height: 700px;
  }

  .interviewBanner {
    height: 257px;
    bottom: 0;
    left: -50px;
  }

  .discBlock {
    width: 284px;
    top: 30px;
  }

  .title {
    font-size: 24px;
    line-height: 32px;
  }

  .textDisc {
    font-size: 17px;
    line-height: 24px;
  }

}

@media (min-width: 1029px) {

  .mainBanner {
    height: 468px;
    top: 0;
    right: 0;
  }

  .banner {
    display: flex;
    position: relative;
    background-color: #d6e6f3;
    width: 100%;
    height: 468px;
    justify-content: center;
    margin: 0 -128px;
    padding: 0 128px;
  }

  .bannerContent {
    composes: content;
  }

  .content {
    position: relative;
    overflow: hidden;
    width: 100%;
    max-width: 1024px;
  }

  .homeSection {
    width: 424px;
    top: 92px;
    left: 0;
  }

  .bannerTitle {
    font-size: 46px;
    line-height: 52px;
  }

  .bannerSubTitle {
    font-size: 17px;
    width: 295px;
  }

  .interviewDisc {
    justify-content: center;
    height: 445px;
  }

  .interviewBanner {
    height: 445px;
    left: -170px;
  }

  .discBlock {
    width: 429px;
    top: 30px;
    right: 0;
  }

  .title {
    font-size: 36px;
    line-height: 48px;
  }

  .textDisc {
    font-size: 17px;
    line-height: 24px;
  }

}

.homeSection {
  position: absolute;
}

.mainBanner {
  position: absolute;
}

.bannerTitle {
  font-weight: bold;
  color: #333333;
}

.bannerSubTitle {
  color: #333333;
  margin-top: 16px;
}

.joinButton {
  margin-top: 36px;
}

.interviewDisc {
  position: relative;
  background-color: #ffffff;
  display: flex;
}

.interviewBanner {
  position: absolute;
}

.discBlock {
  position: absolute;
}

.title {
  font-weight: bold;
  color: #333333;
}

.textDisc {
  margin-top: 24px;
}





