@media (max-width: 1028px) {

  .content {
    justify-content: center;
    align-items: center;
    margin: 0 -20px;
    padding: 0 20px;
    flex-direction: column;
    text-align: center;
    height: 570px;
    display: flex;
    position: relative;
    background-color: #dce4e7;
    width: 100%;
  }

  .mainBanner {
    bottom: 0;
    width: 100%;
    max-height: 368px;
    max-width: 558px;
  }

  .homeSection {
    top: 30px;
    width: 264px;
  }

  .bannerTitle {
    font-size: 24px;
    line-height: 32px;
  }

  .bannerSubTitle {
    font-size: 15px;
    margin-top: 20px;
    width: 250px;
  }

}

@media (min-width: 1029px) {

  .banner {
    height: 468px;
    margin: 0 -128px;
    padding: 0 128px;
    justify-content: center;
    display: flex;
    position: relative;
    background-color: #dce4e7;
    width: 100%;
  }

  .content {
    position: relative;
    width: 100%;
    max-width: 1024px;
  }

  .mainBanner {
    height: 468px;
    top: 0;
    right: 0;
  }

  .homeSection {
    width: 424px;
    top: 92px;
    left: 0;
  }

  .bannerTitle {
    font-size: 46px;
    line-height: 52px;
  }

  .bannerSubTitle {
    font-size: 17px;
    margin-top: 16px;
    width: 295px;
  }
}



.featureIcon {
  width: 104px;
  height: 104px;
  align-items: center;
  background-repeat: no-repeat;
  background-position: center;
}

.stepIcon {
  width: 64px;
  height: 64px;
  align-items: center;
  background-repeat: no-repeat;
  background-position: center;
}

.homeSection {
  position: absolute;
}

.mainBanner {
  position: absolute;
}


.bannerTitle {

  font-weight: bold;
  color: #333333;
}

.bannerSubTitle {
  color: #333333;
}

.joinButton {
  margin-top: 36px;
}


