.vacancyLinks {
  display: none;
}

@media (max-width: 1028px) {

  .footer {
    height: 100px;
    padding: 25px 46px;
    flex-direction: column;
  }

  .footerLink {
    font-size: 12px;
    line-height: 16px;
  }

  .footerYear {
    font-size: 12px;
    line-height: 16px;
    margin-right: 8px;
  }

  .footerLogo {
    height: 25px;
  }

  .header {
    height: 59px;
  }

  .navItems {
    display: none;
  }

  .navItem {
    margin-bottom: 230px;
    font-size: 13px;
  }

  .mobileNavItems {
    position: absolute;
    background-color: #ededed;
    top: 0;
    right: 0;
    width: 40%;
    min-height: 100vh;
    padding: 24px;
  }

  .headerTop {
    padding: 0 20px;
  }

  .changeLocationModal {
    top: 80px;
    width: 330px;
    padding: 15px;
    left: 7%;
  }

  .content {
    padding: 0 20px;
  }

  .navDropdown {
    position: absolute;
    padding-right: 25px;
    overflow: hidden;
    top: 100px;
  }

  .closeButton {
    position: absolute;
    font-size: 25px;
    top: 7px;
    right: 20px;
  }

  .headerCity {
    display: none;
  }

  .changeLocationBtn {
    display: none;
  }

  .locationIcon {
    height: 16px;
    margin-left: 110px;
  }

  .modalCityName {
    font-size: 15px;
    line-height: 20px;
  }

  .modalTitle {
    font-size: 30px;
    line-height: 32px;
  }

  .modalCityMain {
    font-size: 18px;
  }

}

@media (min-width: 1029px) {

  .footer {
    height: 116px;
    padding: 0 128px;
  }

  .footerLink {
    font-size: 17px;
    line-height: 1.41;
  }

  .footerYear {
    font-size: 17px;
    line-height: 1.41;
    margin-right: 12px;
  }

  .header {
    height: 110px;
  }

  .navItems {
    display: flex;
  }

  .navItem {
    margin-right: 24px;
    font-size: 12px;
  }

  .headerTop {
    padding: 0 128px;
  }

  .changeLocationModal {
    top: 205px;
    width: 720px;
    padding: 32px;
    left: 33%;
  }

  .content {
    padding: 0 128px;
  }

  .navDropdown {
    border-radius: 12px;
    box-shadow: 0 12px 36px 0 rgba(0, 0, 0, 0.5);
    background-color: #ffffff;
    top: 60px;
    padding: 24px;
  }

  .mobileNavItems {
    display: none;
  }

  .dropdownItemLink {
    font-size: 17px;
    font-weight: 600;
    line-height: 1.41;
  }

  .menuButton {
    display: none;
  }

  .modalCityName {
    font-size: 17px;
    line-height: 1.41;
  }

  .modalTitle {
    font-size: 46px;
    line-height: 1.13;
  }

  .modalCityMain {
    font-size: 21px;
  }

}

.footerLink {
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  letter-spacing: normal;
  color: #ffffff;
}

.footer {
  width: 100%;
  margin-top: auto;
  background: #252525;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.footerYear {

  font-weight: normal;
  font-style: normal;
  font-stretch: normal;

  letter-spacing: normal;
  color: #a8a8a8;

}

.header {
  background: #fff;
  border-bottom: 1px solid rgba(136, 141, 148, 0.2);
  z-index: 1;
}

.headerTop {
  height: 60px;
  border-bottom: 1px solid rgba(136, 141, 148, 0.2);
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.headerCity {
  font-size: 12px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.33;
  letter-spacing: 0.6px;
  color: #333333;
  margin-left: 4px;
}

.headerLocation {
  display: flex;
  align-items: flex-start;
}

.changeLocationOverlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 3;
}

.changeLocationModal {
  position: absolute;
  background: #fff;
  z-index: 4;
  box-sizing: border-box;
  border-radius: 12px;
  box-shadow: 0 12px 36px 0 rgba(0, 0, 0, 0.5);
}

.locationModalCities {
  display: flex;
  flex-wrap: wrap;
}

.modalCityIcon {
  background: url("~images/ic-pin.svg") no-repeat;
  width: 24px;
  height: 24px;
  margin-right: 8px;
}

.modalCityName {
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  letter-spacing: normal;
  color: #333333;
}


.modalCity {
  width: 45%;
  margin-right: 5%;
  margin-bottom: 36px;
  display: flex;
  align-items: center;
  cursor: pointer;
}

.modalHeader {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 40px;
}

.modalClose {
  background: url("~images/close.svg") no-repeat;
  width: 40px;
  height: 40px;
  cursor: pointer;
}

.modalTitle {
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  letter-spacing: normal;
  color: #333333;
}

.changeLocationBtn {
  cursor: pointer;
  font-size: 12px;
  color: #a0a09f;
}

.main {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 100%;
  font-family: 'Open Sans', sans-serif;
}

.content {
  overflow: auto;
  background: #fff;
}

.navItems {
  align-items: center;
  height: 50px;
  padding-left: 128px;
  position: relative;
}

.navItem {
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: 2;
  letter-spacing: 0.5px;
  color: #333333;
  text-decoration: none;
  text-transform: uppercase;
  padding: 12px 0;
  border-bottom: 4px solid transparent;
  cursor: pointer;
}

.navItemActive {
  border-bottom-color: #2ca53a;
}

.navDropdown {
  position: absolute;
  box-sizing: border-box;
}

.dropdownItem {
  margin-bottom: 20px;
}

.dropdownItemLink {
  font-style: normal;
  font-stretch: normal;
  letter-spacing: normal;
  color: #333333;
  text-decoration: none;
}

.dropdownItem:last-child {
  margin-bottom: 0;
}
